import React, { Component } from 'react';
import './App.css';


import Select from 'react-select';
import Table from './components/Table'
import { fb } from './firebase.service'
const mentors = require('./data.json');


class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: null,
            data: mentors,
            selectedOption: JSON.parse(localStorage.getItem('mentor')) || null
        }
    }

    handleChange = (selectedOption) => {
        this.setState({ selectedOption });
        localStorage.setItem('mentor', JSON.stringify(selectedOption));
        console.log(`Option selected:`, selectedOption);
    }

    login = () => {
        fb.login().then(({ user }) => {
            fetch(`https://api.github.com/user/${user.providerData[0].uid}`)
                .then(response => response.json())
                .then(result => {
                    const mentor = mentors.find(mentor => mentor.label.toLowerCase() === result.login.toLowerCase());
                    if (mentor) {
                        this.setState({selectedOption: mentor});
                        localStorage.setItem('mentor', JSON.stringify(mentor));
                    }
                });
            this.setState({ user });
        })

    }

    logout = () => {
        fb.logout().then(() => {
            this.setState({ user: null });
        });
    }

    render() {
        const { user } = this.state;
        
        return (
            <>
                <section className="login__wrapper">
                    {user && <img src={user.photoURL} alt='avatar' />}
                    {user
                        ? <button onClick={this.logout}>Logout</button>
                        : <button onClick={this.login}>Login</button>
                    }

                <Select
                    value={this.state.selectedOption}
                    onChange={this.handleChange}
                    options={this.state.data}
                    className="Select"
                    
                />
                </section>
                {this.state.selectedOption && 
                    <Table students={this.state.selectedOption.students} /> }
            </>
        );
    }
}

export default App;
