/* eslint-disable no-fallthrough */
import React from "react"; 

export default function StatusCell ({status}) { 

    let cellStyle;
    switch(status) {
        case "Checking":
        cellStyle = {
            backgroundColor: '#e99898',
        }
        break
        case 'Checked':
        cellStyle = {
            backgroundColor: '#d8e9d3',
        };
        break
        case 'ToDo':
        cellStyle = {
            backgroundColor: '#cbcbcb',
        };
        break
        case 'In Progress':
        cellStyle = {
            backgroundColor: '#fef1cc',
        };
        break
        default:
        cellStyle = {
            backgroundColor: '#94b1e0',
        };
    } 

    return (
        <>
            <td style={cellStyle}>{status}</td>
        </>
    );

};
