import React from "react"; 
import uuid from 'uuid';

import StatusCell from './StatusCell'

const taskArray = require('../tasks.json');

export default function TableContents ({students}) { 
    const tableContent = [];
    for (let task of taskArray) {
        const index = taskArray.indexOf(task);

        const taskStatusRow = [];
        for (let student of students) {
            let status;
            const defaultStatus = taskArray[index].status;
            if (student.tasks.length) {
                const taskToCheck = student.tasks.find(item => item.name === taskArray[index].name)
                status = (taskToCheck) ? taskToCheck.status : defaultStatus;
            } else status = defaultStatus;
            taskStatusRow.push(
                <StatusCell status={status} key={uuid.v4()} />
            )
        }
        
        tableContent.push(
            <tr key={uuid.v4()}>
                <td><a href={task.link}>{task.name}</a></td>
                {taskStatusRow}
            </tr>

        )
    }

    return (
        <>
            {tableContent}
        </>
    );
};
