import React from "react";
import uuid from 'uuid';


import StudentCell from './StudentCell'
import TableContents from './TableContents'

export default function TableHeader ({students}) {
return (
        <table>
            <thead>
                <tr>
                    <th key={uuid.v4()}></th>
                    {students.map(student => <StudentCell student={student} key={uuid.v4()}/>)}
                </tr>
            </thead>
            <tbody>
                <TableContents students={students} />
            </tbody>
        </table>
    );

};
